@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
*{
  margin: 0%;
  padding: 0%;
  font-family: 'Noto Sans JP', sans-serif;
  scroll-behavior: smooth;
}


header {
    position: fixed;
    top: 0%;
    font-family: 'Noto Sans JP', sans-serif;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 5;
    box-shadow: 0px 1px 3px rgb(219, 219, 219);
}

.left {
    margin: 25px 0px 35px 70px;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 600;
    font-size: 1.2rem;
}

.left .list {
    display: flex;
    list-style: none;
}

.left .list #h3r{
    display: none;
}

.left .list a {
    position: relative;
    margin-right: 50px;
    text-decoration: none;
    color: #0D2533;
}

.left .list li a::after {
    content: "";
    position: absolute;
    left: 0%;
    bottom: -10px;
    width: 0%;
    height: 3px;
    background-color: rgb(0, 119, 255);
    transition: all 0.5s;
}

.left .list li:hover a::after {
    width: 100%;
    transition: all 0.5s;
}

.right a{
    position: relative;
    margin-right: 5rem;
    color: #0D2533;
    display: flex;
    font-size: 1.3rem;
    cursor: pointer;
    font-weight: bold;
    text-decoration: none;
}

.right a::after:nth-child(1){
    content: "";
    position: absolute;
    left: 0%;
    bottom: -10px;
    width: 0%;
    height: 3px;
    background-color: rgb(0, 119, 255);
    transition: all 0.5s;
}
.right:hover a::after:nth-child(1){
    width: 100%;
    transition: all 0.5s;
}

.right div
{
    display: none;
    cursor: pointer;
}

ul 
{
    display: flex;
    list-style: none;
}

.drawer {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.952);
  display: grid;
  place-items: center;
  position: fixed;
  width: 100%;
  left: -100%;
  transition: 0.5s;
  z-index: 1;
  top: 0px;
}

.drawer.active {
  left: 0;
  transition: 0.5s;
}

#dul {
  list-style: none;
  color: white;
  height: 60%;
  width: 100%;
  display: grid;
  place-items: center;
  padding-top: 18%;
}

#dul li a {
  text-decoration: none;
  padding: 10px;
  font-size: x-large;
  color: white;
}
#h3r
{
    height: 30px;
    width: 30px;
}
/* Media Query */
@media screen and  (max-width:1132px) {

    .right
    {
        margin-right: 30px;
    }

    .left .list #h3r{
        display: block;
    }
    .left
    {
        margin: 20px 30px;
    }
    .right div
    {
        display: block;
    }

    .right #h3
    {
        display: none;
    } 
    
    .left .list #h3r
    {
        padding: 0px;
    }

    .left .list li
    {
        display: none;
    }
    
}
.LandingPage {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    font-size: 4rem;
    font-family: 'Poppins', sans-serif;
    padding: 45px 0;
    height: 100vh;
    /* margin-top: 50px; */
}

.leftc
{
    color: #0D2533;
    padding: 20px;
}

.leftc .one {
    margin: 5px auto;
}

.two {
    margin: 5px auto;
}

.three {
    margin: 5px auto;
}

.four
{
    padding-top: 20px;
    padding-bottom: 50px;
}

.four button {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 10px;
    border: 0;
    padding: 12px 40px;
    background-color: rgb(0, 119, 255);
    color: white;
    font-family: 'Quicksand', sans-serif;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.four button:hover
{
    background-color: blue;
    box-shadow: 2px 2px 10px 3px rgb(41, 36, 36);
    transition: all 0.3s ease-in-out;
}

.four a
{
    text-decoration: none;
    width: -webkit-max-content;
    width: max-content;
}

.img img {
    height: 28rem;
    padding: 40px;
}

#name {
    color: rgb(0, 119, 255);
}

.logo .logol
{
    display:flex;
    align-items: center;
    justify-content: space-between;
    width: 40%;
}

.logo img
{
    display: none;
}

.icons svg
{
    cursor: pointer;
}


.icons svg:hover path 
{
    fill: rgb(0, 119, 255);
}


/* Media Query  */
@media screen and (max-width:1100px) {
    .LandingPage
    {
        width: 100%;
        height: 100vh;
        display: grid;
        place-items: center;
    }

    .leftc
    {
        font-size: 3rem;
    }

    .leftc .four 
    {
        padding: 20px 0 30px 0;
    }

    .LandingPage .leftc .logo
    {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .LandingPage .leftc .logo .logol
    {
        display: grid;
        grid-gap: 1rem;
        height: -webkit-max-content;
        height: max-content;
    }

    .logo img
