        display: none;
    }
}

@media screen and (max-width:600px) {
    
    .LandingPage 
    {
        height: 90vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .leftc
    {
        font-size: 2.3rem;
    }
    
